//FORM.scss
.bv-form{
	font-family:$spectral;
	.form-group{
		position:relative;
		margin-bottom: 2rem;
		label{
			font-size: 1.125rem;
		}
		&.widget-label{
			margin-bottom: 0;
		}
	}
	.form-control{
		border:none;
		border-bottom:1px solid #979797;
		background-color:$input-bg;
		border-radius: $input-border-radius;
		&:focus{
			box-shadow: none;
		}
	}
	.form-control-lg{
		height: 50px;
	}
	.btn-group{
		margin-top:3rem;
	}
}
.bv-form-message{
	padding:2rem;
	margin:auto;
	max-width:500px;
	text-align: center;
	margin-bottom: 2rem;
}
.bv-form-result{
	display: none;
}
.form-control::placeholder{ /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: $placeholder-color;
  opacity: 1; /* Firefox */
}
:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: $placeholder-color;
}
::-ms-input-placeholder { /* Microsoft Edge */
  color: $placeholder-color;
}
select:not([multiple]) {
    -webkit-appearance: none;
    -moz-appearance: none;
    background-position: right 50%;
    background-repeat: no-repeat;
    background-image: url('data:image/svg+xml;utf8,<?xml version="1.0" encoding="utf-8"?><!DOCTYPE svg PUBLIC "-//W3C//DTD SVG 1.1//EN" "http://www.w3.org/Graphics/SVG/1.1/DTD/svg11.dtd"><svg xmlns="http://www.w3.org/2000/svg" width="14" height="12" version="1"><path d="M4 8L0 4h8z"/></svg>');
}
.form-select{
	position:relative;
	height: 73px;
	overflow: hidden;
	background-color:#fafafa;
	small{ background-color: #fff }
	&:after{
		top:20px;
		right:1rem;
		display: block;
		content: "";
		position:absolute;
		background-image:url('data:image/png;base64,');
		width: 19px;
		height: 12px;
		background-size:100% 100%;
		background-repeat:no-repeat;
		//transition: transform .2s ease-out;
	}
	&.no-arrows{
		&:after{display: none !important;}
	}
	&.active{
		&:after{
			transform: rotate(180deg);
		}
	}
	&:before{
		content: "";
		display: block;
		position:absolute;
		background-color:#fff;
		width: 100%;
		height: 100%;
		top:0;
		left:0;
		z-index: 3;
	}
	&.initialized{
		height: auto;
		overflow: auto;
		&:before{
			display: none;
		}
	}
	input.form-control{
		padding-right: 3rem;
		position:relative;
		background-color:transparent;
		z-index: 2;
		cursor: pointer
	}
}
.mbsc-bootstrap{
	.popover{
		background-color:#fafafa;
		@media screen and (max-width: $screen-mobile ){
    		border-top-left-radius:1rem !important;
    		border-top-right-radius:1rem !important;
    	}
    	@media screen and (min-width: $screen-tablet ){
    		padding:2rem;
    	}
	}
	.popover-content{
		@media screen and (max-width: $screen-mobile ){
    		border-top-left-radius:1rem !important;
    		border-top-right-radius:1rem !important;
    	}
    	background-color:#fafafa;
	}
	.mbsc-fr-btn{
		font-size: 1.25rem;
		font-family: $lato;
	}
	.mbsc-cal-hdr{
	    font-family: $spectral;
	    padding: .75rem 0;
	    font-weight: bold;
	    font-size: 1.5rem;
	    line-height: 1.5;
	}
	.mbsc-fr-btn-cont{
		margin-top: 1rem
	}
	@media screen and (min-width: $screen-tablet ){

		.mbsc-fr-btn{
			font-size: 16px;
		}
	}
	@media screen and (min-width: $screen-desktop-xs ){
		.popover{
			padding:0;
			max-width: 100%;
			.mbsc-fr-arr-i{

    		background-color:transparent;
			}
		}
		.popover-content{
			padding:1rem;
		}
		.mbsc-fr-overlay{
			background:none;
		}
	}
} 
.mbsc-outline{
	.popover-header{
	    font-size: 1.5rem;
	    line-height: 1.5;
	    padding:0.75rem;
	    font-weight: bold;
	}
	
	.popover{
		border: 0;
    	margin: 0;
    	height: 75vh;
    	background-color:transparent;
	}
	.mbsc-w-p{
		padding:1rem;
	}
	.popover-content{
	    height: 100%;
	}
	.mbsc-fr-bubble-bottom,.bottom,.bs-popover-bottom{
		//margin-top:-50px;
		.mbsc-fr-arr-w{
			visibility: hidden;
		}
	}
	.mbsc-fr-btn-cont{
		padding:.85rem;
		position: absolute;
    	bottom: 0;
	}
	.mbsc-fr-w{
		font-family:$spectral;
	}
	.mbsc-sc-whl-gr{
		padding:0;
	}
	.mbsc-sc-whl-w{
		margin:0;
	}
	.arrow{
		&:after{
			 border-top-color: #fafafa;
		}
	}
	.mbsc-sc-itm{
	    //padding: 0.5rem 1rem !important;
	    border-bottom: dashed 1px #aaa;
	    font-size: 1.25rem;
	    line-height: 1.5;
	    &.mbsc-sc-itm-sel{
	    	color:$dust;
	    	font-weight: bold;
	    }
	}
	@media screen and (min-width: $screen-tablet ){
		.popover{
	    	background-color:#fafafa;
	    	padding:2rem;
	    	box-shadow:3px 3px 5px rgba(0,0,0,0.3);
	    }
	    .mbsc-sc-whl{
	    	margin-top: 1rem;
	    	margin-bottom: 2rem
	    }
		.mbsc-fr-popup{
			height:auto;
		}
		.mbsc-fr-btn-cont{
			padding:0;
		}
		.mbsc-w-p{padding:0};
		.mbsc-fr-btn-cont{
			position: relative;
		}
		.mbsc-sc-itm{
		    //padding: 0.5rem 1rem !important;
		    border-bottom: dashed 1px #aaa;
		    font-size: 1.25rem;
		    line-height: 1.5;
		    &.mbsc-sc-itm-sel{
		    	background-color:#ececec;
		    	//color:#fff;
		    }
		}
	}
	@media screen and (min-width: $screen-desktop-xs ){
		.popover{
			padding:0;
			height:auto;
		}
		.popover-content{
			padding:0;
		}
	    .mbsc-sc-whl{
	    	margin-top:0;
	    	margin-bottom:0;
	    	// padding-top:1.5rem;
	    	// padding-bottom:1.5rem
	    }
		.mbsc-fr-overlay{
			background:none;
		}
	}
}