//TAGS.scss

.bv-tags-container{
	display: flex;
	flex-wrap:wrap;
	margin-left:-.25rem;
	margin-right:-.25rem;
	.tag{
		flex:50% 1 1;
		max-width: 50%;
		padding:0 .25rem;
		.bv-icon{
			margin-right: .5rem;
		}
	}
	.tag-dust{
		text-align:center;
		display: flex;
		justify-content: center;
		align-items:center;
		margin-bottom: 0.5rem;
		font-family: $lato;
		font-size:18px;
		background-color:$dust;
		color:#fff;
		padding:.5rem 1rem;
	}
	@media screen and (min-width: $screen-tablet ){
		margin-left:-.5rem;
		margin-right:-.5rem;
		.tag{
			flex:25% 1 1;
			max-width: 25%;
			padding:0 .5rem;
		}
		.tag-dust{
			margin-bottom: 1rem;
		}
	}
	@media screen and (min-width: $screen-desktop-xs ){
		width: 80%;
		.tag{ 
			flex:33.33% 1 1;
			max-width: 33.33%;
		}
	}
}

