.bv-brands-ad-container{
	background-color:$dust;
	.bv-cards-container{
		.bv-card{
			flex:0 0 50%;
			max-width: 50%;
			height:120px;
			display: flex;
			justify-content: center;
			align-items: center;
			img{
				max-width: 108px;
			}
		}
	}
	@media screen and (min-width: $screen-tablet){
		width: 90%;
		margin: auto;
		margin-bottom: 1rem;
		.bv-cards-container{
			.bv-card{
				flex:0 0 20%;
				max-width: 20%;
				height: 110px;
				margin-bottom: 0;
				img{
					max-width: 85px;
				}
			}
		}
	}
	@media screen and (min-width: $screen-desktop-s){
		.bv-cards-container{
			.bv-card{
				height: 120px;
				img{
					max-width: 100px;
				}
			}
		}
	}
	@media screen and (min-width: $screen-desktop){
		width: 75%;
	}
}
