.bv-events-container{
	padding-left:15px;
	padding-right:15px;
	.bv-filter-select-group{
		display: flex;
		flex-wrap:wrap;
		margin-left:-15px;
		margin-right: -15px;
		margin-bottom: 2rem;
		>.form-group{
			flex:100% 0 0;
			padding-left:15px;
			padding-right:15px;
		}
	}
	.event-endDate{
		display: none;
	}
	@media screen and (min-width: $screen-desktop-xs ){
		padding-left:0;
		padding-right:0;
		.bv-filter-select-group{
			>.form-group{
				flex:33.33% 0 0;
			}
		}
	}
}
.bv-events-list-container{
	&.events-grouped{
		.bv-events-group{
			border-top:1px dashed #555;
			padding-top:2rem;
		}
	}
	font-size:14px;
	.bv-cards-container{
		margin-left:-15px;
		margin-right:-15px;
	}
	.bv-card{
		height: auto;
	}
	.bv-card-info{
		padding:0.75rem 0;
		overflow: hidden;
	}
	.bv-events-group{
		padding:2rem 0;
		padding-top:0;
		.bv-events-group-text{
			font-size: 26px;
			font-family: $spectral;
			margin-bottom:2rem;
		}
		.bv-cards-container{
			padding:0;
		}
	}
	.bv-card-open-time{
		font-family: $spectral;
		font-size: 14px;
		line-height:1.5;
		color:#4a4a4a;
		margin-top: .35rem;
		margin-bottom: .35rem;
		opacity: 0;
		&.formalized{
			opacity: 1;
		}
	}
	.bv-event-location{
		font-size: 14px;
		margin-bottom: .5rem;
		font-weight: bold;
		display: block;
		color:#000;
	}
	a.bv-event-location{
		&:hover,&:focus{
			text-decoration: none;
			color:$dust;
		}
	}
	.bv-card-title{
		margin-bottom: 0.75rem;
	}
	.bv-card-text{
		font-family: $lato;
		color:#4a4a4a;
		line-height:22px;
	}
	@media screen and (min-width: $screen-tablet ){
		.bv-card-info{
			padding:0.5rem 0;
			overflow: hidden;
		}
		.bv-event-location{
			bottom:27px;
		}
	}
}
//cards anime
.bv-events-container{
	.bv-card{
    	opacity: 1;
    	&.visible{
			  -webkit-animation: popup 0.25s ease-out both; /* Safari 4+ */
			  -moz-animation:    popup 0.25s ease-out both; /* Fx 5+ */
			  -o-animation:      popup 0.25s ease-out both; /* Opera 12+ */
			  animation:         popup 0.25s ease-out both; /* IE 10+, Fx 29+ */
    	}
	}
}
.bv-filter-select-group{
	::-webkit-input-placeholder { /* Edge */
	  color: #495057;
	}

	:-ms-input-placeholder { /* Internet Explorer 10-11 */
	  color: #495057;
	}

	::placeholder {
	  color: #495057;
	}
}
.mbsc-fr-bubble{
	z-index: 98
}
