//BANNER.scss

.bv-banner{
	background-position: center center;
	background-size:cover;
	background-repeat:no-repeat;
	display: flex;
	justify-content: center;
	align-items: flex-end;
	color:#fff;
	color:#fff;
	height: 288px;
	width: 100%;
	.title{
		font-family: 'Spectral', serif;
		font-size: 1.75rem;
		letter-spacing: 3px;
		margin-bottom:3.5rem;
		a{
			color:#fff;
			&:hover,&:focus{
				text-decoration: none;
				opacity: 0.75
			}
		}
	}
	&.banner-link{
		display: flex;
		justify-content: center;
		align-items: center;
		height: 250px;
		background-size:auto 100%;
		background-position:50% 50%;
		position: relative;
		.title{
			margin-bottom:0;
			text-align:center;
			z-index: 1;
			max-width: 90%;
		}
		&:hover,&:focus{
			text-decoration: none;
			color:#fff;
		}
		.banner-image{
			position:absolute;
			height: 100%
		}
	}
	@media screen and (min-width: $screen-mobile-s ){
		&.banner-link{
			.title{
				max-width: 80%;
			}
		}
	}
	@media screen and (min-width: $screen-desktop-xs ){
		&.banner-link{

		}

	}
	@media screen and (min-width: $screen-desktop ){
		height: 450px;
		&.banner-link{
			background-size:cover;
			height: 300px;
			.banner-image{
				width: 100%;
				height: 100%;
				object-fit: cover
			}
		}
	}
}

.bv-banner-image{
	@media screen and (min-width: $screen-desktop ){
		img{width: 100%;}
	}
}




