//ARTICLES.scss
.bv-article{
	display: flex;
	flex-wrap: wrap;
	padding:2rem 0;
	>div{
		padding-left: 15px;
		padding-right: 15px;
	}
	.bv-article-info,.bv-article-gallery-container{
		flex:100% 1 1;
	}
	.bv-article-info{
		display: flex;
		flex-direction: column;
		color:#4a4a4a;
		font-size: 14px;
		font-family: $lato;
	}
	.bv-article-title{
		font-weight: bold;
		font-size: 30px;
		line-height: 40px;
		color:#000;
		margin-bottom: 3rem;
	}
	.bv-article-container{

	}
	.bv-article-paragraph{
		margin-bottom: 2rem;
	}
	h3{
		color:#000;
		font-size: 14px;
		margin-bottom: 1.5rem;
		font-weight: bold;
	}
	.bv-article-time{
		.event-start-time{
			+.event-end-time{
				&:before{
					content:"~";
					display: inline-block;
					margin:0 0.5rem;
				}
			}
		}
	}
	
	.bv-shop-menu{
		display: flex;
		flex-direction: row;
		align-items:flex-start;
		line-height: 1.5;
		margin-bottom: 2rem;
		h3{
			line-height: 1.645;
			margin-right: .75rem;
		}
	}
	.bv-shop-menu-content{

	}
	.bv-shop-tags{
		margin-left:-.5rem;
		margin-right:-.5rem;
		margin-top: -.5rem;
		display: flex;
		flex-direction:row;
		flex-wrap: wrap;
		>.bv-tag{
			padding:0 .5rem;
			display: flex;
			position: relative;
			margin-bottom: .5rem;
			align-items:flex-end;
			img{
				width: 30px;
				height: 30px;
				margin-right: 2px;
			}
		}
		.tag-coffee{
			padding-left:22px;
			.icon-coffee{top:-10px;}
		}
		.tag-breakfast{
			padding-left:25px;
			.icon-breakfast{top:2px;}
		}
		.tag-dinner{
			padding-left:28px;
			.icon-dinner{top:1px;}
		}
	}
	.bv-shop-docs{
		a{
			color:$dust;
			text-decoration: underline;
			&:hover,&:focus{
				color:$dust-hover;
			}
		}
	}
	.bv-dashed-link{
		margin-bottom: 2rem;
	}
	.bv-gallery{
		margin-bottom: .5rem;
	}
	@media screen and (min-width: $screen-tablet ){
		.bv-gallery{
			margin-bottom: 1rem;
		}
	}
	@media screen and (min-width: $screen-desktop-xs ){
		margin-left: -15px;
		margin-right: -15px;
		>.bv-article-info,.bv-article-gallery-container{
			flex:50% 0 0;
			max-width: 50%;
		}
	}
}