//NAVBAR.scss

.bv-navbar{
	position: fixed;
	z-index: 99;
	width: 100%;
	font-family: $navbar-font-family;
	padding: 1rem 1.5rem;
	background-color:transparent;
	&.navbar-lock-top{
		position:fixed !important;
		background-color:rgba(0,0,0,.75);
		padding: 0.8rem 1.5rem;
		top:0;
		.bv-logo{
			width: 64px;
			height: 21px;
			background-image:url('/TCBWeb/img/logo-white-small.png')
		}
	}
	.navbar-right-link{
		flex-direction: row;
		align-items:center;
		>li{
			padding:0 0.5rem;
			&:last-child{
				padding-left:0;
				padding-right:0;
			}
		}
		.bv-hamburger{
			margin-bottom: 2px;
			margin-right: -12px;
		}
	}
	.bv-logo-container{
		top:0;
		left: 0;
		padding:1.875rem;
		position:absolute;
		width: 100%;
		display: flex;
		justify-content: center;
		.bv-logo{
			margin-right:0
		}
	}
	.bv-opentime{
		display: none;
	}
	.navbar-collapse{
		opacity:0;
		z-index: 99;
		padding: 2rem;
		padding-top:2.825rem;
	    position: fixed;
	    background-color: #000;
	    top: 0;
	    bottom: 0;
	    right: 0;
	    overflow-y: auto;
	    width: $navbar-collapse-width;
	    transform: translateX($navbar-collapse-width);
	    display: block !important;
	    transition: transform 0.125s ease-in;
	    &.collapsing{

	    }
	    &.show{
	    	opacity:1;
	    	transform: translateX(0);
	    }
	    .tta-navbar-nav-wrap{
	    	display: flex;
	    	justify-content: center;
	    	height: 100%;
	    	.container{
	    		width:50%;
	    		align-self:center;
	    	}
	    	.row{
	    		flex-flow:row nowrap;
	    	}
	    }
	    .navbar-nav{
    		.nav-link{
    			font-size: $nav-link-size;
    			line-height: $nav-link-line-height;
    			color:$nav-link-color;
    			padding: 0.75rem 0;
				&:hover,&:focus{
					color:$nav-link-color-hover;
				}	
    		}
	    	.active{
	    		.nav-link{
	    			color:#fff;
	    		}
	    	}
	    }
	}
	.bv-lang-option{
		.lang-link{
		    font-size: 1.125rem;
		    line-height: 1.2rem;
		    margin:0.8rem 0;
		    color: $nav-link-color;
			display: inline-block;
			position: relative;
			&:hover,&:focus{
				color:$nav-link-color-hover;
				text-decoration: none;
			}
			+.lang-link{
				margin-left: 10px;
				&:before{
					display: block;
					content: "";
					border-left:1px solid $nav-link-color;
					height: 100%;
					position:absolute;
					left:-7px;
					top:-0.5px;
				}
			}
			&.cn{
				font-family: $notosanssc;
			}
			&.active{
				opacity: 0.5;
				pointer-events: none;
				&:hover,&:focus{
					color: $nav-link-color;
				}
			}
		}
	}
	@media screen and (min-width: $screen-tablet ){
		padding:2.875rem 1.875rem;
		.navbar-collapse{
		    width: $navbar-collapse-md-width;
		    transform: translateX($navbar-collapse-md-width);
		}
		&.navbar-lock-top{
			.bv-logo{
				margin-left: -32px;
			}
		}
		.bv-logo{
			position:absolute;
			left:50%;
			margin-right: auto;
			margin-left: -108px;
		}
		.bv-opentime{
			font-size: 12px;
			display: flex;
			color:#fff;
			font-weight: 100;
			font-family: $opentime-font-family;
			span{
				margin-left: 8px;
				font-weight: 600;
			}
			.widget-body{
				&:before,&:after{
					content: "";
					display: inline-block;
					position:relative;
					top:-3px;
					width: 15px;
					border-top:1px solid #fff;
				}
				&:before{margin-right: 5px;}
				&:after{margin-left: 5px;}
			}
		}
	}	
}