
html,body{
	overflow-x:hidden;
}

body{
	background-color:$body-bg;
	&.body-lock{
		overflow: hidden;
	    position: absolute;
	    top: 0;
	    bottom: 0;
	    left: 0;
	    right: 0;
	    height: auto;
	}
}



*{box-sizing: border-box }

ul{ list-style-type: none; padding-left:0; }


h1,h2,h3{
	font-family: $spectral;
}

.bv-loading-mask{
	position:fixed;
	top:0;
	left:0;
	background: #333;
	width: 100vw;
	height: $ios-screen-height;
	z-index: 10000;
	display: flex;
	justify-content: center;
	align-items:center;
	@media screen and (min-width: $screen-tablet ){
		height:$ios-screen-md-height
	}
	@media screen and (min-width: $screen-desktop-xs ){
		height: 100vh;
	}
}


//common
.bv-logo{
	width: 144px;
	height: 56px;
	background-size:100% 100%;
	text-indent: -9999px;
	background-image:url('/TCBWeb/img/logo-white.svg');
	@media screen and (min-width: $screen-desktop-xs ){
		width: 216px;
		height: 84px;
	}
}


//section
.bv-section{
	padding:3rem 0;

	&.fullscreen{
		padding:4rem;
		width: 100vw;
		height: 100vh;
		display: flex;
		justify-content: flex-start;
		align-items:flex-end;
	}
	&.section-large{
		padding:2rem 0;
	}
	@media screen and (min-width: $screen-tablet ){
		padding:$section-padding;
		&.section-large{
			padding:4rem 0;
		}
	}
}
.bv-section-info{
	padding:3rem 0;
	text-align: center;
	&.info-half-screen{
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		text-align: left;
		padding:2rem;
		.bv-section-description{
			margin:0;
		}

	}
	@media screen and (min-width: $screen-tablet ){
		padding:4rem 0;
	}
}

.bv-section-title{
	font-size: 1.625rem;
	font-family: $spectral;
	margin-bottom: 0;
	font-weight: bold;
	&.section-title-white{
		color:white;
		font-weight: 300;
	}
	+.bv-section-description{
		margin-top: 1rem;
	}
	a{
		color:#212529;
		&:hover,&:focus{
			opacity: 0.7;
			text-decoration: none;
		}
	}
}

.bv-section-description{
	margin:auto;
	font-size: 1rem;
	font-family:$lato;
	line-height: 1.375rem;
	margin-bottom: 2rem;
	color:#4a4a4a;
	padding-left:1rem;
	padding-right:1rem;
	@media screen and (min-width: $screen-tablet ){
		padding-left:0;
		padding-right:0;
		max-width: 60%;
	}
}

.bv-section-content{
	padding-left:1rem;
	padding-right:1rem;
	@media screen and (min-width: $screen-desktop-xs){
		padding-left:0;
		padding-right:0;
	}
}

blockquote{
	width: 80%;
	margin:auto;
	padding:0 1rem;
	display: block;
	font-family: $spectral;
	font-size: 1.125rem;
	line-height: 28px;
	position:relative;
	text-align: center;
	p{display: inline }
	&:before,&:after{
		position:absolute;
		display: inline-block;
		content: "";
		width: 30px;
		height: 26px;
		background-size:100% 100%;
		background-image:url('data:image/png;base64,')
	}
	&:before{
		left:-15px;
		//margin-right: 1rem;
	}
	&:after{
		right:-15px;
		//margin-left: 1rem;
		transform: rotate(180deg);
	}
	@media screen and (min-width: $screen-tablet ){
		padding:0 2rem;
		width: 65%;
		font-size: 1.25rem;
		line-height: 40px;
	}
}



//utilities
.color-dust{
	color:$dust;
}
.bg-grey{
	background-color:#ececec;
}
.bg-color-dust{
	background-color:$dust;
}

.collapsing {
    -webkit-transition: none;
    transition: none;
    display: none;
}


.bv-404-template{
	width: 100vw;
	height: 100vh;
	display: flex;
	align-items:center;
	justify-content: center;
	background-image:url(/TCBWeb/img/kv-banner/kv3.png);
	background-size:cover;
}