//CARDS.scss
.bv-cards-container{
	display: flex;
	flex-wrap: wrap;
	.bv-card{
		padding-right: 15px;
    	padding-left: 15px;
    	margin-bottom: 30px;
    	position: relative;
    	//height:418px;
    	overflow: hidden;
    	flex:0 0 100%;
    	max-width: 100%;
	}
	.bv-card-img{
		width: 100%;
		display: block;
		position:relative;
		padding-bottom:100%;
		background-size:cover;
		background-position:50% 50%;
		background-repeat:no-repeat;
		background-color:#ccc;
		position:relative;
		img{
			width: 100%;
			height: 100%;
			position:absolute;
			object-fit: cover;
		}
	}
	.bv-card-icon{
		width: 75px;
		height: 75px;
		margin:auto;
		display: flex;
		justify-content: center;
		align-items:center;
		img{
			width: 100%;
			
		}
	}
	.bv-card-cate{
		text-align: center;
		padding:0.625rem;
		color:$dust;
		font-family: $spectral;
		font-style: italic;
		position:relative;
		font-size: 12px;
		&:before,&:after{
			border-top:1px solid $dust;
			width: 5px;
			top:-3px;
			content: "";
			display: inline-block;
			position:relative;
		}
		&:before{margin-right:5px;}
		&:after{margin-left:5px;}
	}
	.bv-card-info{
		display: block;
		font-family: $spectral;
		padding:0.625rem 0;
	}
	.bv-card-title{
		font-weight: bold;
		font-family: $spectral;
		font-size: 1.125rem; 
		margin-bottom: 1rem;
		color:#000;
	}
	a.bv-card-title{
		display: block;
		&:hover,&:focus{
			text-decoration: none;
			opacity: 0.6
		}
	}
	.bv-card-text{
		color:#909090;
		font-family: 'Lato', serif;
		font-size:0.925rem;
		line-height: 1.25;
	}
	.bv-card-link{
		padding:0;
		font-size:12px;
	}
    @media screen and (min-width: $screen-tablet ){
    	.bv-card{
    		flex:0 0 50%;
    		max-width: 50%;
    		margin-bottom:60px;
    	}
		.bv-card-link{
			//position: absolute;
			bottom:0;
		}
    }
    @media screen and (min-width: $screen-tablet ) and (max-width: 991px){
    	.bv-card{
    		flex:0 0 50%;
    		max-width: 50%;
    		margin-bottom:60px;
    	}
    }
    @media screen and (min-width: $screen-desktop-xs ){
	    margin-right: -15px;
	    margin-left: -15px;
    	.bv-card{
    		flex:0 0 33.33%;
    		max-width: 33.33%;
    		//height:450px;
    	}
    }
}
//cards anime
.bv-cards-animation{
	.bv-card{
    	opacity: 0;
    	&.visible{
			  -webkit-animation: popup-from-bot 0.25s ease-out both; /* Safari 4+ */
			  -moz-animation:    popup-from-bot 0.25s ease-out both; /* Fx 5+ */
			  -o-animation:      popup-from-bot 0.25s ease-out both; /* Opera 12+ */
			  animation:         popup-from-bot 0.25s ease-out both; /* IE 10+, Fx 29+ */
    	}
	}
	@media screen and (min-width: $screen-tablet ) and (max-width: 991px){
		.bv-card{
	    	&.visible{
				&:nth-child(2n+1) {-webkit-animation-delay: 0.15s;}
				&:nth-child(2n+2) {-webkit-animation-delay: 0.3s;}
	    	}
		}
	}
	@media screen and (min-width: $screen-desktop-xs ){
		.bv-card{
	    	&.visible{
				&:nth-child(3n+2) {-webkit-animation-delay: 0.15s;}
				&:nth-child(3n+3) {-webkit-animation-delay: 0.3s;}
	    	}
		}
	}
}
