//FOOTER.scss
.bv-footer{
	padding:2rem 1rem;
	color:$footer-color;
	background-color:$footer-bg;
	font-family: $padauk;
	.bv-footer-primary{
		margin-bottom: 1.5rem;
		.custom-padding-left{
			padding-left:0
		}
	}
	.bv-links-group-container{
		display: flex;
		flex-direction: row;
		margin-bottom: 1rem;
		.bv-links-group{
			flex:50% 1 1;
		}
	}
	.bv-links-group{
		font-size: 14px;
		margin-bottom: 1.5rem;
		>li{
			width: 50%;
		}
		a{ 
			color:#fff;
		    font-size: 14px;
		    line-height: 24px;
		    padding:0;
		    &:hover,&:focus{
		    	color:$dust;
		    	text-decoration:none;
		    }
		}
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;
		max-height:180px;
	}
	.bv-contact-info{
		font-family: $lato;
		font-size: 14px;
		font-weight: bold;
		margin-bottom: 2rem;
		.title{
			font-size: 1.125rem;
			font-family: $padauk;
			margin-bottom: 0;
		}
		.address{
			color:$dust;
			line-height: 1.2
		}
		p{margin-bottom: 0.75rem}
	}
	.bv-map-link{
		margin-bottom: 2rem;
		display: block;
		position:relative;
		font-family: $padauk;
		color:#fff;
		&:before{
			display: flex;
			justify-content: center;
			align-items:center;
			position:absolute;
			content: attr(data-link-text);
			width: 100%;
			height: 100%;
			font-size: 1.75rem;
			position: absolute;
			background-color:rgba(0,0,0,0.33);
			top:0;
		}
		img{
			width: 100%;
		}
	}
	.bv-social-links{
		margin-top:1rem;
		.bv-links-group-row{
			display: flex;
			flex-direction: row;
			flex-wrap: nowrap;
			margin-bottom: 0;
			align-items: center;
			justify-content: space-between;
			margin-left:-.5rem;
			margin-right:-.5rem;
			margin-bottom:.25rem;
			.link-location{
				font-size: 1.75rem
			}
			>li{
				padding-left:.5rem;
				padding-right:.5rem;
			}
		}
		a{ color:#fff;font-size:2rem; }
	}
	.bv-copyright{
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		font-size: 13px;
		font-family: $padauk;
		@media screen and (min-width: $screen-mobile-s){
			font-size: 14px;
		}
	}
	@media screen and (min-width: $screen-mobile-xs){
		.bv-copyright{font-size: 14px;}
	}
	@media screen and (min-width: $screen-tablet ){
		padding: 4rem 2.5rem;
		.bv-footer-primary{
			.custom-padding-left{
				padding-left:15px
			}
		}
		.bv-contact-info{
			margin-bottom: 3.25rem;
			.title{
				margin-bottom: .25rem;
			}
		}
		.bv-links-group{
			>li{
				width: 52%
			}
			max-height:180px;
		}
	}
	@media screen and (min-width: $screen-desktop-xs ){
		.bv-footer-primary{
			.custom-padding-left{
				padding-left:0
			}
		}
		.bv-links-group{
			max-height: 220px;
		}
	}
}

