//LINKS.scss
.bv-link-read-more{
	color:#666;
	font-family: $spectral;
	font-style:italic;
	font-size:12px;
	display: inline-block;
	border-top:1px dashed #666;
	border-bottom:1px dashed #666;
	line-height: 1.75;
	z-index: 1;
	&:hover,&:focus{
		text-decoration:none;
		color:$dust;
		border-top:1px dashed $dust;
		border-bottom:1px dashed $dust;
	}
}

.bv-dashed-link{
	color:#666;
	font-family: $spectral;
	font-size:18px;
	display: inline-block;
	text-align:center;
	border-top:1px dashed #666;
	border-bottom:1px dashed #666;
	padding:1.25rem 0;
	font-style:italic;
	border-left:none;
	border-right:none;
	&:hover,&:focus{
		text-decoration:none;
		color:$dust;
		border-top:1px dashed $dust;
		border-bottom:1px dashed $dust;
		outline: 0
	}
	&.disabled{
		opacity: 0.5;
		cursor: not-allowed;
		&:hover,&:focus{
			color:#666;
		}
	}
}


a.color-dust{
	&:hover,&:focus{
		text-decoration:none;
		color:$dust-hover;
	}
}