//IMAGES.scss
.bv-article-gallery-container{
	max-width: 100%;
	a{
		display: block
	}
	.img-wrap{
		padding-bottom:100%;
		position:relative;
		img{
			position:absolute;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}	
}
.bv-gallery{
	max-height: 660px;
	overflow: hidden;
}
.bv-full-image{
	margin-bottom: 2rem;
	img{
		width: 100%;
	}
}
.bv-gallery-nav{
	.slick-list{
	margin-left:-.25rem;
	margin-right: -.25rem;	
	}
	.slick-track{
		margin-right: unset;
		margin-left: unset;
	}
	.slick-slide{
		padding:0 .25rem;
	}
	.slick-arrow{
		width: 11px;
		height: 29px;
	    display: flex;
	    align-items: center;
	    justify-content: center;
    	&:before{
			width: 11px;
	    	height: 29px;
	    	background-repeat: no-repeat;
	    	background-size: 100% 100%;
			background-image: url('data:image/png;base64,');
			content: ""
		}
	}
	.img-wrap{
		background-color: $image-holder-bg-color;
		&:after{
			content: "";
			display: block;
			position: absolute;
			width: 100%;
			height: 100%;
			left:0;
			top:0;
			//background-color:rgba(0,0,0,0.3);
			box-shadow: inset 0 0 20px 0 rgba(0,0,0,0.75);
			opacity: 0;
			transition:all .1s ease-in;

		}
		&:hover{
			&:after{opacity: 1}
		}	
		&:focus{
			box-shadow: none;
			outline: 0
		}
	}
	@media screen and (min-width: $screen-tablet ){
		&.centered{
			margin-left:1.5rem;
			margin-right:1.5rem;	
		}
		.slick-list{
		margin-left:-.5rem;
		margin-right: -.5rem;	
		}
		.slick-slide{
			>div{
				padding:0 .5rem;
			}
			
		}	
		.slick-arrow.slick-prev{
			left: -1.5rem;
		}
		.slick-arrow.slick-next{
			right: -1.5rem;
		}
		.slick-prev, .slick-next{
			top:45%;
		}
	}
}


.bv-googlemap-container{
	padding-bottom:124%;
	position:relative;
	margin-bottom:2rem;
	iframe{
		position:absolute;
		width: 100%;
		height: 100%;
	}
	@media screen and (min-width: $screen-tablet ){
		padding-bottom:55%;
		margin-bottom: 3rem;
	}
}


.bv-googlemap-info-container{
	font-family: $lato;
	margin-bottom:2rem;
	.h3{
		margin-bottom: 1rem;
	}
	.address{
		color:$dust;
		font-size: 18px;
		line-height:1.25;
		margin-bottom: 1rem;
	}
	p{
		color:#989898;
		font-size:1rem;
	}
	.row{
		>div{
			margin-bottom: 2rem;
		}
	}
	@media screen and (min-width: $screen-tablet ){
		.h3{
			margin-bottom: 2rem;
		}
		.address{ font-size: 24px; line-height:1.5; }
		
	}
}

