//animation.scss
@-webkit-keyframes phAnimation {
  0% {
    transform: translate3d(-30%, 0, 0); }
  100% {
    transform: translate3d(30%, 0, 0); } }

@keyframes phAnimation {
  0% {
    transform: translate3d(-30%, 0, 0); }
  100% {
    transform: translate3d(30%, 0, 0); } }

//popup-from-bot
@-webkit-keyframes popup {
  0% {
    transform: scale(0.5);}
  100% {
    transform: scale(1);
    }
}

@keyframes popup {
  0% {
    transform: scale(0.5);}
  100% {
    transform: scale(1);
    }
}

@-webkit-keyframes popup-bot {
  0% {
    transform: translateY(20px);
    visibility:hidden;}
  100% {
    transform: translateY(0);
    visibility:visible;} }

@keyframes popup-bot {
  0% {
    transform: translateY(20px);}
  100% {
    transform: translateY(0); } }

@-webkit-keyframes popup-from-bot {
  0% {
    transform: translateY(20px);
    opacity: 0 }
  100% {
    transform: translateY(0);
    opacity: 1 } }

@keyframes popup-from-bot {
  0% {
    transform: translateY(20px);
    opacity: 0 }
  100% {
    transform: translateY(0);
    opacity: 1 } }

@-webkit-keyframes popup-from-left {
  0% {
    transform: translateX(-20px);
    opacity: 0.5 }
  100% {
    transform: translateX(-0);
    opacity: 1 } }

@keyframes popup-from-left {
  0% {
    transform: translateX(-20px);
    opacity: 0.5 }
  100% {
    transform: translateX(-0);
    opacity: 1 } }


