//ALART.scss
.bv-alert{
	color:$dust;
	background-color: $alert-default-bg;
	width: 80%;
	padding:2rem 3rem;
	h1.h1{
		font-family:$lato;
		margin-bottom: 2rem;
		font-size: 1.5rem;
	}
	text-align:center;
	.alert-icon{
		font-size: 4rem;
		color:$dust;
	}
	.btn{
		padding:1rem 2rem;
	}
	@media screen and (min-width: $screen-tablet ){
		width: 60%;
		h1.h1{font-size: 2.5rem;}
		.alert-icon{
			font-size: 4rem;
		}
		.btn{
			padding:1rem 2.5rem;
		}
	}
	@media screen and (min-width: $screen-desktop-s ){
		width: 50%;
	}
}
.bv-fancybox-alert{
	color:#666;
	font-family: $spectral;
	font-size:20px;
	text-align: center;
	.alert-text{
		max-width: 315px;
		margin:auto;
		padding:1.5rem 0;
	}
}
.form-log{
	font-size: 14px;
	max-width: 300px;
	margin:auto;

}
.bv-alert-overlay{
	display: none;
	position:fixed;
	left:0;
	right:0;
	bottom:0;
	top:0;
	background-color:rgba(0,0,0,0.5);
	z-index: 9998;
	opacity: 0;
	transition: opacity 0.25s ease-in-out;
	&.active{
		display: block;
		opacity: 1;
	}
}
.bv-cookie-alert{
	font-size: 12px;
	width: 100vw;
	height: auto;
	background-color:#222;
	color:#fff;
	padding:.75rem;
	position: fixed;
	bottom:0;
	left:0;
	z-index: 9999;
	display: none;
	p{margin-bottom: .5rem}
	.btn{
		float:right;
		font-size: 12px;
		padding:.5rem;
		line-height: 1;
		border-radius: 3px;
	}
	&.active{
		-webkit-animation: popup-bot 0.5s ease-in-out;
		animation: popup-bot 0.5s ease-in-out;
		-webkit-animatio-fill-mode:both;
		animation-fill-mode:both;
		display: block;
	}
	@media screen and (min-width: $screen-desktop-s ){
		
		border-radius:6px;
		max-width: 350px;
	    right: 0;
	    left: initial;
	    margin: 1rem;
		
	}
}