//CAROUSEL.scss
@-webkit-keyframes fade-in-from-bot {
  0% {
  	opacity: 0;
    transform: translateY(20px); }
  100% {
  	opacity: 1;
    transform: translateY(30); } 
}
.bv-carousel{
	background-color:#333;
	position:relative;
	height: $ios-screen-height;
	max-height: $ios-screen-height;
	overflow: hidden;
	visibility: hidden;
	&.slick-initialized{
		visibility:visible;
		max-height:none;
		overflow:inherit;
	}
	.slide{
		position: relative;
		width: 100%;
		height: $ios-screen-height;
		img{height: 100%;}
	}
	.slick-active{
		.bv-carousel-caption{
			-webkit-animation: fade-in-from-bot 0.5s ease-out .5s;
	    	animation: fade-in-from-bot 0.5s ease-out .5s;
	    	-webkit-animatio-fill-mode:both;
	    	animation-fill-mode:both;
	    	opacity: 1;
		}
	}
	.slide-img{
		height: 100%;
		width: 100%;
		background-repeat:no-repeat;
		background-size:auto 100%;
		background-position:50% 50%;
		@media screen and (min-width: $screen-desktop-xs){
			background-size:cover;
		}
	    picture{
			position: absolute;
		    top: 50%;
		    left: 50%;
		    -webkit-transform: translate(-50%,-50%);
		    transform: translate(-50%,-50%);
		    min-width: 100%;
		    min-height: 100%;
		    object-fit: cover;
		    img{
		    	position: absolute;
		    	width: 100%;
		    	height: 100%;
		    	left:0;
		    	top:0;
		    	object-fit: cover;
		    }
	    }
	}
	.slide-video{
	    position: absolute;
	    top: 0;
	    left: 0;
	    right: 0;
	    bottom: 0;
	    overflow: hidden;
	    video{
			position: absolute;
		    top: 50%;
		    left: 50%;
		    -webkit-transform: translate(-50%,-50%);
		    transform: translate(-50%,-50%);
		    min-width: 100%;
		    min-height: 100%;
		    object-fit: cover;
	    }
	    &:after{
	    	content: "";
	    	display: block;
	    	width: 100%;
	    	height: 100%;
	    	top:0;
	    	left:0;
	    	position:absolute;
	    	background-color:rgba(0,0,0,0.3)
	    }
	}
	.carousel-indicators{
		bottom:-60px;
		li{
			background-color:#C9C9C9;
			opacity: 1;
		    width: 30px;
		    height: 2px;
		    margin-right: 5px;
		    margin-left: 5px;
			&.active{
				background-color:#000;
			}
		}
	}
	.bv-carousel-caption{
		z-index:2;
		color:#fff;
		position:absolute;
		bottom:10%;
		text-align:center;
		opacity: 0;
		padding:0 2rem;
		display: block;
		width: 100%;
		transition: opacity 0.2s ease-out;
		&:hover,&:focus{
			text-decoration: none;
			outline: 0;
			cursor: pointer;
			opacity: 0.5 !important;
		}
		.title{
			font-family: $spectral;
			font-size: 2rem;
		}
		.summary{
			font-family: $lato;
			font-size: 1rem;
		}
	}
	@media screen and (min-width: $screen-tablet){
		height: $ios-screen-md-height;
		max-height: $ios-screen-md-height;
		.slide{
			height: $ios-screen-md-height;
			max-height: $ios-screen-md-height;
		}
		.bv-carousel-caption{
			width: 500px;
			left:50%;
			margin-left:-250px;
			bottom:12.5%;
			.title{
				font-size: 2.5rem;
			}
			.summary{
				font-size: 1.25rem;
			}
		}
	}
	@media screen and (min-width: $screen-desktop-xs){
		height: 100vh;
		max-height: 100vh;
		.slide{
			height: 100vh;
			max-height: 100vh;
		}
	}
}

.slick-prev,.slick-next{z-index: 1;}
.slick-next{right:25px;}
.slick-prev{left:25px;}
.slick-dots{
	bottom:-50px;
	li{
	    width: 30px;
	    height: 2px;
	    margin-right: 5px;
	    margin-left: 5px;
	    padding:0;
	    button{
			background-color:#C9C9C9;
			opacity: 1;
		    width: 30px;
		    height: 2px;
		    padding:0;
		    &:before{
		    	display: none;
		    }
	    }
	    &.slick-active{
			button{
				background-color:#000;
			}
		}
	}
} 

.slick-arrow{
    width: 100px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
	&:before{
		content: "";
		width: 11px;
		height: 29px;
		opacity: 0.5;
		transform:translate(0);
		background-size:100% 100%;
		background-image:url('data:image/png;base64,');
		display: block;
	}
	&.slick-prev{
		left:0;
		&:before{
			transform:scaleX(-1);
		}
	}
	&.slick-next{
		right:0;
	}
	&:hover,&:focus{
		&:before{
			opacity: 1;
			background-size: 100% 100%;
			background-image:url('data:image/png;base64,');
		}
	}

}
.slick-arrow-black{
	.slick-arrow{
		width: 11px;
		height: 29px;
	    display: flex;
	    align-items: center;
	    justify-content: center;
    	&:before{
			width: 11px;
	    	height: 29px;
	    	background-repeat: no-repeat;
	    	background-size: 100% 100%;
			background-image: url('data:image/png;base64,');
			content: ""
		}
	}
}
