.bv-offers-container{
	.bv-card{
		height: auto;
		position:relative;
	}
	.bv-card-info{
		width: 80%;
		margin:auto;
		border:1px solid #d8d8d8;
		border-top:0;
		padding:1rem .25rem;
		padding-top:0.5rem;
		text-align:center;
	}
	.bv-card-title{
		font-family: $lato;
		font-weight: bold;
		margin-bottom: 0;
		font-size: 16px;
	}
	.bv-card-open-time{
		color:#4a4a4a;
		font-family: $lato;
		font-size: 12px;
		top:1.75rem;
		position: absolute;
		background-color: #fff;
		z-index: 1;
		padding:.25rem .625rem;
		&:after{
			content: "";
			display: inline-block;
			position: absolute;
		    top: 0;
		    right: -13px;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 26px 13px 0 0;
			border-color: #ffffff transparent transparent transparent;
		}
	}
	@media screen and (min-width: $screen-tablet ){
		.bv-card{
			height: 469px;
		}
	}
	@media screen and (min-width: $screen-desktop-xs ){
		padding-left:0;
		padding-right:0;
		.bv-card-info{
			padding:0.75rem;
			max-height: 119px;
			overflow-y: hidden;
		}
	}
}