.bv-about-kv{
	height: $ios-screen-height;
	display: flex;
	justify-content: flex-start;
	padding:2rem;
	align-items: flex-end;
	.bv-section-info{
		padding:1.5rem;
		display: inline-flex;
		background-color:#fff;
		width: auto;
		flex-direction: column;
		justify-content: flex-start;
		text-align: left;
	}
	.bv-section-title{
		font-size: 20px;
		line-height: 1.5;
	}
	.bv-section-description{
		font-size: 16px;
		line-height: 24px;
		margin-bottom: 0;
		color:#4a4a4a;
		padding-left:0;
   		padding-right:0;
	}
	@media screen and (min-width: $screen-tablet ){
		height: $ios-screen-md-height;
		padding:4rem;
		.bv-section-info{
			padding:2.5rem;
			flex-direction: row;
		}
		.bv-section-title{
			font-size: 30px;
			flex:40% 0 0;
			line-height: 40px;
			+.bv-section-description{
				margin-top: 0;
			}
		}
		.bv-section-description{
			flex:60% 0 0;
			padding-left:1rem;
			max-width: none;
			padding-left: 1rem;
    		padding-right: 1rem;
		}
	}
	@media screen and (min-width: $screen-desktop-xs ){
		height: 100vh;
		.bv-section-info{
			flex:60% 0 0;
		}
	}
}
.bv-about-vision{
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	margin-bottom: 2rem;
	.bv-section-info{
		justify-content: center;
		text-align: left;
		margin:auto;
		padding:4rem 2rem;
	}
	.bv-section-description{
		max-width: none;
		padding-left: 0;
    	padding-right: 0;
    	margin-bottom: 0;
	}
	.bv-section-image{
		padding-right: 2rem;
	}
	@media screen and (min-width: $screen-tablet ){
		margin-bottom: 4rem;
		.bv-section-description{
			padding-left: 1rem;
    		padding-right: 1rem;
    		margin-bottom: 2rem;
		}
		.bv-section-image{
			padding-right:0
		}
	}
	@media screen and (min-width: $screen-desktop-xs ){
		flex-direction: row;
		.bv-section-image,.bv-section-info{
			flex:50% 0 0;
		}
		.bv-section-info{
			padding:4rem;
		}
		.bv-section-description{
			padding-left: 0;
		}
	}
}
.bv-about-motto{
	margin-bottom: 3rem;
	.icon-large-central{
		margin-bottom: 1rem;
		transform: scale(0.7);
	}
	@media screen and (min-width: $screen-tablet ){
		.icon-large-central{
			margin-bottom: 4rem;
			transform:scale(1);
		}
	}
}
.bv-about-carousel{
	position:relative;
	.slick{
		width: 100%;
		margin:auto;
	}
	.slick-list{
		overflow: visible;
	}
	.slick-slide{
		padding-left:.5rem;
		padding-right:.5rem;
		&:focus{
			box-shadow: none;
			outline: 0
		}
	}
	.slick-prev.slick-arrow{left:52px;}
	.slick-next.slick-arrow{right:52px;}
	@media screen and (min-width: $screen-tablet ){
		.slick{width: 100%}
		.slick-slide{
			padding-left:2rem;
			padding-right:2rem;
		}
	}
	@media screen and (min-width: $screen-desktop-xs ){
		.slick{
			width: 962px;
			margin:auto;
		}
	}
}
