/*css version 2020-5-18 1.1 update*/ 

@import url('https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,600|Lato|Noto+Sans+SC:400,700|Noto+Serif+SC:400,600,700|Padauk:400,700|Spectral:400,400i,700&display=swap');

$black:	     #000000 !default;
$white:	     #ffffff !default;
$dark:       #333333 !default;
$dust:       #BB9B5C !default;
$dust-hover: #a08143 !default;
$concrete:   #EAE8E3 !default;
$tiffany:    #6ABCCE !default;

//font setting
$padauk:     'Padauk', serif;
$josefin:    'Josefin Sans', serif;
$notosanssc: 'Noto Sans SC', serif;
$spectral: 	 'Spectral', serif;
$lato:		 'Lato', serif;;


$body-bg: $white;

//navbar
$opentime-font-family:$josefin;
$navbar-font-family:'Padauk','Noto Sans SC',serif;
$nav-link-size:1.125rem;
$nav-link-line-height:1.625rem;
$nav-link-color:$dust;
$nav-link-color-hover:$white;
$navbar-collapse-width:275px;
$navbar-collapse-md-width:275px;

//section
$section-padding:3rem 0;


//banner
$banner-sm-height:      288px !default;
$banner-lg-height:      450px !default;

//
$footer-bg: $black !default;
$footer-color: #ffffff !default;


// -- meida query -- //
$screen-mobilex-xs:   	320px !default;
$screen-mobile-xs:   	375px !default;
$screen-mobile-s: 	 	414px !default;
$screen-mobile: 	 	576px !default;
$screen-tablet: 	 	768px !default;
$screen-desktop-xs:  	992px !default;
$screen-desktop-s:   	1024px !default;
$screen-desktop:     	1200px !default;
$screen-desktop-m:     	1280px !default;
$screen-desktop-lg:  	1344px !default;
$screen-desktop-xlg: 	1400px !default;



//ios setting
$ios-screen-height: 	calc( 100vh - 75px ) !default;
$ios-screen-md-height: 	calc( 100vh - 53px ) !default;


//
$image-holder-bg-color: #cccccc !default;

//form
$input-bg:     			#fafafa !default;
$input-border-radius:   0px !default;
$placeholder-color:     #d9d9d9 !default;


//alert
$alert-default-bg:     rgba(255,255,255,0.85) !default;