//BUTTONS.scss

.btn{
	border-radius: 0;
	font-family: $padauk;
}
.btn-widthfree{
	width: 100%;
	padding:0.969rem 1.25rem;
    font-size: 1.125rem;
    letter-spacing: 2px;
}
.btn-primary{
	color:#fff;
}

.btn-large{
	width: 315px;
}

.btn-dark{
	border-width:1px;
	border-style:solid;
	border-color:#fff;
	&:hover,&:focus{
		color:$dust;
		border-color:$dust;
	}
}

.btn-outline-warning{
	&:hover,&:focus{
		color:#fff;
		border-color:#fff;
	}
}
.btn-group{
	margin:1rem 0;
	display: flex;
	justify-content: center;
	&.double-btn{
		flex-wrap: wrap;
		>a{
		 	flex:100% 0 0;
		 	margin:0 .5rem;
		}
		@media screen and (min-width: $screen-tablet ){
			flex-wrap: nowrap;
			>a{
			 	flex:50% 1 1;
			 	+a{
			 		margin-right: 1rem;
			 	}
			 	&:last-child{
			 		margin-right: 0
			 	}
			}
		}
	}
}